a {
  text-decoration: none;
  color: #000;
}

a:hover {
  text-decoration: none;
  color: #000;
}

button,
input,
optgroup,
select,
textarea {
  font-family: "Roboto", sans-serif;
}

.btn-dark-blue {
  color: #fff;
  background-color: #0000cd;
  border-color: #0000cd;
}

.btn-primary {
  color: #fff;
  background-color: #2196f3 !important;
  border-color: #2196f3 !important;
}

.bg-primary {
  background-color: #2196f3 !important;
}

.bg-transparent {
  background-color: rgba(150, 150, 150, 0.2) !important;
}

.text-primary {
  color: #2196f3 !important;
}

.conteudo {
  background-image: url("./recursos/img/bg.jpeg");
  min-height: 100vh;
}

.w-90 {
  width: 90% !important;
}

.w-95 {
  width: 90% !important;
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: white;
  background-color: #2196f3 !important;
}
