.wrapper {
  display: flex;
  width: 100%;
}

#sidebar {
  min-width: 250px;
  max-width: 250px;
  min-height: 100vh;
  color: #fff;
  transition: all 0.3s;
}

#sidebar .sidebar-header {
  padding: 20px;
}

#sidebar ul.components {
  padding: 20px 0;
}

#sidebar ul li a {
  padding: 10px;
  font-size: 1.1em;
  color: #fff;
  display: block;
}

#sidebar ul li a span:hover {
  color: #fff;
}

.active-link > .fa-angle-left {
  display: block;
}

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
}

#sidebar.active {
  margin-left: -250px;
}

#cabecalhoMenuLateral {
  display: none;
}

@media (max-width: 414px) {
  #sidebar {
    max-width: 100%;
    min-width: 100%;
    display: block;
    position: fixed;
    z-index: 999;
    min-height: 100%;
  }
  #sidebar.active {
    left: -1000px;
    transition: opacity 0.4s ease-out;
    -ms-transition: opacity 0.4s ease-out;
    -moz-transition: opacity 0.4s ease-out;
    -webkit-transition: opacity 0.4s ease-out;
  }
  #cabecalhoMenuLateral {
    display: block;
  }
}

@media (max-width: 768px) {
  #sidebar.active {
    margin-left: 0;
  }
}
